<template>
  <el-main>
    <h4>一些辅助工具，不断添加ing</h4>
    <el-row :gutter="24">
      <el-col :span="6" class="box-card">
        <a href="https://oakspc.gitbook.io/airdrop-handbook" target="_blank" style="text-decoration:none;color:inherit;">
          <el-card>
            <div class="title">空投手册</div>
            <div class="desc">极致精简任务列表，精品号必备</div>
          </el-card>
        </a>
      </el-col>
      <el-col :span="6" class="box-card">
        <div data-path="random" @click="handleClick('/random')">
          <el-card>
            <div class="title">随机化工作</div>
            <div class="desc">随机化账号任务安排，反一致性检测</div>
          </el-card>
        </div>
      </el-col>
      <el-col :span="6" class="box-card">
        <div data-path="wallet" @click="handleClick('/wallet')">
          <el-card>
            <div class="title">ethers.js</div>
            <div class="desc">利用ethers.js批量生成钱包，靓号钱包</div>
          </el-card>
        </div>
      </el-col>
      <el-col :span="6" class="box-card">
        <div data-path="balance" @click="handleClick('/balance')">
          <el-card>
            <div class="title">批量查余额</div>
            <div class="desc">批量查询zks指定token是否有余额</div>
          </el-card>
        </div>
      </el-col>
    </el-row>
  </el-main>
</template>

<script>

export default {
  name: "Tools",
  methods: {
    // 跳转工具
    handleClick(path) {
      this.$router.push(path);
    }
  }
}
</script>

<style lang="stylus" scoped>
.el-main
  width 800px
  margin 0 auto

.box-card:hover
  cursor pointer

.title
  font-size 18px
  font-weight bold

.desc
  margin-top 5px
  font-size 14px
  line-height 1.5
  color grey

</style>



